import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select, Icon, DatePicker } from 'antd';
import { teachers } from 'dump/data';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    sys: '',
    khoa: '',
    course: '',
    specialize: '',
    class: '',
    studentCode: '',
    name: '',
    birthday: '',
    sex: '',
    birthplace: '',
    policyObject: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddStudent extends Component {
  state = {
    teacherId: true,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = teachers.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Thêm sinh viên' : 'Thêm sinh viên'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Hệ" {...formItemLayout}>
              {getFieldDecorator('sys', {
                initialValue: initialValue.sys,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoa" hasFeedback>
              {getFieldDecorator('khoa', {
                initialValue: initialValue.khoa,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khóa" hasFeedback>
              {getFieldDecorator('course', {
                initialValue: initialValue.course,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Chuyên ngành" hasFeedback>
              {getFieldDecorator('specialize', {
                initialValue: initialValue.specialize,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Lớp" hasFeedback>
              {getFieldDecorator('class', {
                initialValue: initialValue.class,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mã SV" hasFeedback>
              {getFieldDecorator('studentCode', {
                initialValue: initialValue.studentCode,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input addonAfter={<Icon type="setting" />} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Họ và tên" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ngày sinh" hasFeedback>
              {getFieldDecorator('birthdate', {
                initialValue: initialValue.birthdate,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePicker />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Giới tính" hasFeedback>
              {getFieldDecorator('sex', {
                initialValue: initialValue.sex,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nơi sinh" hasFeedback>
              {getFieldDecorator('birthplace', {
                initialValue: initialValue.birthplace,
                rules: [{ type: 'object', message: 'Please select time!' }],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đối tượng chính sách" hasFeedback>
              {getFieldDecorator('policyObject', {
                initialValue: initialValue.policyObject,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="lucy">
                  <Option value="OK">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddStudent {...props} />
  </Page>
));



